<template>
  <div style="display: inline-block; width: 100%; height: 100%;margin-left: 40%;margin-top: 3%;">
    <div style="background-color: #2d8cf0;width: 20%;height: 100%;">
      <p style="font-size: 335%; font-family: 华文行楷;text-align: center">欢迎注册</p >
    </div>
    <div style="width: 20%;margin-top: 20px">
      <Form ref="formInline" :rules="ruleInline" :model="formLogin">
        <FormItem prop="user">
          <Input type="text" v-model="formLogin.user" placeholder="用户名">
          <Icon type="ios-person-outline" slot="prepend"></Icon>
          </Input>
        </FormItem>
        <FormItem prop="phone">
          <Input type="text" v-model="formLogin.phone" placeholder="手机">
          <Icon type="ios-person-outline" slot="prepend"></Icon>
          </Input>
        </FormItem>
        <FormItem prop="country">
          <Input type="text" v-model="formLogin.country" placeholder="国家">
          <Icon type="ios-person-outline" slot="prepend"></Icon>
          </Input>
        </FormItem>
        <FormItem prop="province">
          <Input type="text" v-model="formLogin.province" placeholder="省份">
          <Icon type="ios-person-outline" slot="prepend"></Icon>
          </Input>
        </FormItem>
        <FormItem prop="city">
          <Input type="text" v-model="formLogin.city" placeholder="城市">
          <Icon type="ios-person-outline" slot="prepend"></Icon>
          </Input>
        </FormItem>
        <FormItem prop="address">
          <Input type="text" v-model="formLogin.address" placeholder="详细地址">
          <Icon type="ios-person-outline" slot="prepend"></Icon>
          </Input>
        </FormItem>
        <FormItem>
          <Button type="primary" @click="register" style="margin-left: 25%;width: 50%">注册</Button>
        </FormItem>
      </Form>
    </div>
  </div>
</template>
<script>
  import axios from 'axios'
  import daohang from '../components/daohang.vue'
  export default {
    data () {
      return {
        formLogin: {
          user: '',
          phone: '',
          country: '',
          province:'',
          city:'',
          address:''
        },
        ruleInline: {
          phone: {
            required: true,
            trigger: 'change',
            message: '请输入手机号',
          },
          user: {
            required: true,
            message: '请输入用户名',
            trigger: 'change'
          },
          password: {
            required: true,
            message: '请输入密码',
            trigger: 'change'
          }
        }
      }
    },
    methods: {
      register: function () {
        var phone = this.formLogin.phone
        var user = this.formLogin.user
        var country = this.formLogin.country
        var province = this.formLogin.province
        var city = this.formLogin.city
        var address = this.formLogin.address

        var tmp = this
        if(user === '' || phone === '' || country === '' || province === '' || city === '' || address === ''){
          alert('请输入完整信息')
          return
        }

        var to = global.contractAddress
        var funcName = "login"
        var para = ""
        console.log(para)
        window.postMessage({
          "target": "contentscript",
          "data":{
            "to": to,
            "value": "0",
            "contract":{
              "function":funcName,
              "args":para
            }
          },
          "method": "neb_call"
        }, "*")

        window.addEventListener('message', function(e) {
          if(!!e.data.data.neb_call){
            var s = JSON.stringify(e.data.data.neb_call);
            var m = JSON.parse(s);
            var result  = m.result;
            console.log("注册页面接收到消息："+ result)
            if(result !== '\"抱歉，您尚未注册账户。\"') {
              alert('抱歉，您已经存在账户。')
              tmp.$router.push({path: '/homepage'})
              return
            }else{
              console.log('********* 注册: sendTransaction *****************')
              to = global.contractAddress
              funcName = "register"
              para = "[\"" + user + "\",\"" + phone + "\",\"" + country + "\",\"" + province + "\",\"" + city + "\",\"" + address + "\"]"
              console.log(para)
              window.postMessage({
                "target": "contentscript",
                "data":{
                  "to": to,
                  "value": "0",
                  "contract":{
                    "function":funcName,
                    "args":para
                  }
                },
                "method": "neb_sendTransaction"
              }, "*")

              window.addEventListener('message', function(e) {
                if(!!e.data.data.receipt) {
                  console.log("recived by 注册页面:" + e + ", e.data:"+ JSON.stringify(e.data))
                  tmp.$router.push({ path: '/homepage' })
                  alert('注册成功')
                }
              })
            }
          }
        })

      }
    }
  }
</script>



